.widget {
  background: $light-blue;
  margin-bottom: $xl-size;
}


// widget header
.widget-header {
  background: $blue;
  color: $off-white;
  padding: $m-size;
  display: flex;
  justify-content: space-between;

}

.widget-header__margin {
  margin: 0;
}

// widget message

.widget-message {
  color: $off-white;
  margin: 0;
  padding: $l-size;
  text-align: center;
  border-bottom: 1px solid lighten($light-blue, 10%)
}
